// stories/o-step.stories.tsx
import type { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';
import type { OSkeleton } from '../components/o-skeleton/o-skeleton';

export default {
	title: 'Components/Skeleton',
	argTypes: {},
	args: {}
} as Meta;

// Custom decorator to increase height
const customHeightDecorator = (storyFn: () => any) => {
	return html` <div style="width: 300px; height: 150px">${storyFn()}</div> `;
};

const TemplateDefault: StoryObj<OSkeleton> = {
	decorators: [customHeightDecorator],
	parameters: {
		layout: 'centered'
	},
	render: () => html`
    <o-skeleton></skeleton>
    `
};

const TemplateCard: StoryObj<OSkeleton> = {
	decorators: [customHeightDecorator],
	parameters: {
		layout: 'centered'
	},
	render: () => html`
    <o-card style="box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25); border-radius: 8px; width: 400px; height: auto;">
    <div style="width: 100%; margin-bottom: 10px;">
    <o-skeleton></skeleton>
    </div>
    <div style="width: 100%; margin-bottom: 10px;">
    <o-skeleton></skeleton>
    </div>
    <div style="width: 75%; margin-bottom: 10px;">
    <o-skeleton></skeleton>
    </div>
    </o-card>
    `
};

export const Default: StoryObj = {
	...TemplateDefault,
	args: {},
	parameters: {
		layout: 'centered',
		docs: {
			source: {
				transform: (_, storyContext) => {
					const {} = storyContext;
					//Create Dynamic code example using args
					return `
-----------------------
// Vue Code Snippet
-----------------------

import { OSkeleton } from "@oui/vue";

<o-skeleton></o-skeleton>

-----------------------
// React Code Snippet
-----------------------

import { OSkeleton } from "@oui/react";

<OSkeleton></OSkeleton>
        `;
				}
			}
		}
	}
};

export const SkeletonInCard: StoryObj = {
	...TemplateCard,
	args: {},
	parameters: {
		layout: 'centered',
		docs: {
			source: {
				transform: (_, storyContext) => {
					const {} = storyContext;
					//Create Dynamic code example using args
					return `
-----------------------
// Vue Code Snippet
-----------------------

import { OSkeleton } from "@oui/vue";

<o-card style="box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25); border-radius: 8px; width: 400px; height: auto;">
  <div style="width: 100%; margin-bottom: 10px;">
    <o-skeleton></o-skeleton>
  </div>
  <div style="width: 100%; margin-bottom: 10px;">
    <o-skeleton></o-skeleton>
  </div>
  <div style="width: 75%; margin-bottom: 10px;">
    <o-skeleton></o-skeleton>
  </div>
</o-card>

-----------------------
// React Code Snippet
-----------------------

import { OSkeleton } from "@oui/react";

<OCard style={{ boxShadow: '0px 2px 4px 0px rgba(0, 0, 0, 0.25)', borderRadius: 8, width: 400, height: 'auto' }}>
  <div style={{ width: '100%', marginBottom: 10 }}>
    <OSkeleton />
  </div>
  <div style={{ width: '100%', marginBottom: 10 }}>
    <OSkeleton />
  </div>
  <div style={{ width: '75%', marginBottom: 10 }}>
    <OSkeleton />
  </div>
</OCard>
        `;
				}
			}
		}
	}
};
